import React from 'react';
import { Route } from 'react-router-dom';
import Search from './components/search/search';
import Detailsmovie from './components/detailsmovie/detailsmovie';
import Detailstv from './components/detailstv/detailstv';
import Detailsperson from './components/detailsperson/detailsperson';
import Detailsseason from './components/detailsseason/detailsseason';
import Footer from './components/footer/footer';
import Nav from './components/nav/nav';
import Browse from './components/browse/browse';
import Keyword from './components/keywords/keywords';
import Find from './components/find/find';

function App() {
  return (
    <>
      <Route path='/' component={Nav} />
      <Route exact path='/' component={Search} />
      <Route exact path='/browse' component={Browse} />
      <Route exact path='/find' component={Find} />
      <Route exact path='/keyword/:id' component={Keyword} />
      <Route exact path='/movie/:id' render={(props) => <Detailsmovie {...props} />} />
      <Route exact path='/tv/:id' component={Detailstv} />
      <Route exact path='/tv/:id/season/:idseason' component={Detailsseason} />
      <Route exact path='/person/:id' component={Detailsperson} />
      <Footer />
    </>
  );
}

export default App;
