import React, { useState, useEffect } from 'react';
import {
  ItemWrapper,
  Item,
  Item2,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  Sec,
  SeeMore,
  Posters,
  PosterWrapper,
  CenterTitle,
  PhotoGallery,
  OutWrp,
  CloseButton,
  OpenFull,
  VideoWrapper,
  ResponsiveVideoOuter,
  ResponsiveVideoWrapper,
  MovieInfo2,
  ImagesTrigger,
  SocialHr,
  BackgroundCont,
  GoBack,
  Center,
} from '../styles/searchstyles';
import Grow from '@material-ui/core/Grow';
import EmptyPosterBig from '../../img/empty_poster_b.jpg';
import EmptyActorBig from '../../img/empty_actor_b.jpg';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';

function Browse(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [isLoaded3, setIsLoaded3] = useState(false);
  const [isLoaded4, setIsLoaded4] = useState(false);
  const [isLoaded5, setIsLoaded5] = useState(false);
  const [popular, setPopular] = useState(false);
  const [popularTv, setPopularTv] = useState(false);
  const [topRated, setTopRated] = useState(false);
  const [topRatedTv, setTopRatedTv] = useState(false);
  const [upcoming, setUpcoming] = useState(false);
  const api_key = '58666352553834b5820ef436680fb2e5';
  const getMovieGenres = () => ({
    28: { name: 'Akcja' },
    12: { name: 'Przygodowy' },
    16: { name: 'Animacja' },
    35: { name: 'Komedia' },
    80: { name: 'Kryminał' },
    99: { name: 'Dokumentalny' },
    18: { name: 'Dramat' },
    10751: { name: 'Familijny' },
    14: { name: 'Fantasy' },
    36: { name: 'Historyczny' },
    27: { name: 'Horror' },
    10402: { name: 'Muzyczny' },
    9648: { name: 'Tajemnica' },
    10749: { name: 'Romans' },
    878: { name: 'Sci-Fi' },
    10770: { name: 'film TV' },
    53: { name: 'Thriller' },
    10752: { name: 'Wojenny' },
    37: { name: 'Western' },
  });
  const getTvGenres = () => ({
    10759: { name: 'Akcja i Przygoda' },
    16: { name: 'Animacja' },
    35: { name: 'Komedia' },
    80: { name: 'Kryminał' },
    99: { name: 'Dokumentalny' },
    18: { name: 'Dramat' },
    10751: { name: 'Familijny' },
    10762: { name: 'Dla dzieci' },
    9648: { name: 'Tajemnica' },
    10763: { name: 'Wiadomości' },
    10764: { name: 'Reality' },
    10765: { name: 'Sci-Fi & Fantasy' },
    10766: { name: 'Opera mydlana' },
    10767: { name: 'Rozmowy' },
    10768: { name: 'Wojna i Polityka' },
    37: { name: 'Western' },
  });

  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPosterBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActorBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    }
  };

  useEffect(() => {
    fetch(`https://api.themoviedb.org/3/movie/popular?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setPopular(result);
          setIsLoaded(true);
        },
        (error) => {
          setIsLoaded(false);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/movie/now_playing?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setUpcoming(result);
          setIsLoaded2(true);
        },
        (error) => {
          setIsLoaded2(false);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/movie/top_rated?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setTopRated(result);
          setIsLoaded3(true);
        },
        (error) => {
          setIsLoaded3(false);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/tv/popular?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setPopularTv(result);
          setIsLoaded4(true);
        },
        (error) => {
          setIsLoaded4(false);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/tv/top_rated?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setTopRatedTv(result);
          setIsLoaded5(true);
        },
        (error) => {
          setIsLoaded5(false);
          setError(error);
        }
      );
    window.scrollTo(0, 0);
  }, []);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        <GoBack>
          <ArrowBackIcon
            fontSize='large'
            onClick={() => {
              if (props.history.length > 2) {
                props.history.goBack();
              } else props.history.replace('');
            }}
          />
        </GoBack>
        <BackgroundCont>
          {popular.results.length > 0 && <Sec>Popularne Filmy</Sec>}
          <ItemWrapper>
            {popular.results.slice(0, 18).map((item, index) => {
              item.media_type = 'movie';
              return (
                <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                  <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.title.toLowerCase().split(' ').join('-')}`)}>
                    {havePhoto(item)}
                    <div className='tit2'>
                      {item.title} {item.release_date && `(${item.release_date.slice(0, 4)})`}
                      <br />
                      <span className='character'>
                        {item.genre_ids?.length > 0 && item.genre_ids.map((i) => (getMovieGenres()[i] ? getMovieGenres()[i].name : null)).join(', ')}
                      </span>
                    </div>
                  </Item>
                </Grow>
              );
            })}
          </ItemWrapper>
          {isLoaded2 && (
            <>
              {upcoming.results.length > 0 && <Sec>W kinach</Sec>}
              <ItemWrapper>
                {upcoming.results.slice(0, 18).map((item, index) => {
                  item.media_type = 'movie';
                  return (
                    <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                      <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.title.toLowerCase().split(' ').join('-')}`)}>
                        {havePhoto(item)}
                        <div className='tit2'>
                          {item.title} {item.release_date && `(${item.release_date.slice(0, 4)})`}
                          <br />
                          <span className='character'>
                            {item.genre_ids?.length > 0 &&
                              item.genre_ids.map((i) => (getMovieGenres()[i] ? getMovieGenres()[i].name : null)).join(', ')}
                          </span>
                        </div>
                      </Item>
                    </Grow>
                  );
                })}
              </ItemWrapper>
            </>
          )}
          {isLoaded3 && (
            <>
              {topRated.results.length > 0 && <Sec>Najlepiej Oceniane Filmy</Sec>}
              <ItemWrapper>
                {topRated.results.slice(0, 18).map((item, index) => {
                  item.media_type = 'movie';
                  return (
                    <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                      <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.title.toLowerCase().split(' ').join('-')}`)}>
                        {havePhoto(item)}
                        <div className='tit2'>
                          {item.title} {item.release_date && `(${item.release_date.slice(0, 4)})`}
                          <br />
                          <span className='character'>
                            {item.genre_ids?.length > 0 &&
                              item.genre_ids.map((i) => (getMovieGenres()[i] ? getMovieGenres()[i].name : null)).join(', ')}
                          </span>
                        </div>
                      </Item>
                    </Grow>
                  );
                })}
              </ItemWrapper>
            </>
          )}
          {isLoaded4 && (
            <>
              {popularTv.results.length > 0 && <Sec>Popularne Seriale</Sec>}
              <ItemWrapper>
                {popularTv.results.slice(0, 18).map((item, index) => {
                  item.media_type = 'tv';
                  return (
                    <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                      <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.name.toLowerCase().split(' ').join('-')}`)}>
                        {havePhoto(item)}
                        <div className='tit2'>
                          {item.name} {item.first_air_date && `(${item.first_air_date.slice(0, 4)})`}
                          <br />
                          <span className='character'>
                            {item.genre_ids?.length > 0 && item.genre_ids.map((i) => (getTvGenres()[i] ? getTvGenres()[i].name : null)).join(', ')}
                          </span>
                        </div>
                      </Item>
                    </Grow>
                  );
                })}
              </ItemWrapper>
            </>
          )}
          {isLoaded5 && (
            <>
              {topRatedTv.results.length > 0 && <Sec>Najlepiej Oceniane Seriale</Sec>}
              <ItemWrapper>
                {topRatedTv.results.slice(0, 18).map((item, index) => {
                  item.media_type = 'tv';
                  return (
                    <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                      <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.name.toLowerCase().split(' ').join('-')}`)}>
                        {havePhoto(item)}
                        <div className='tit2'>
                          {item.name} {item.first_air_date && `(${item.first_air_date.slice(0, 4)})`}
                          <br />
                          <span className='character'>
                            {item.genre_ids?.length > 0 && item.genre_ids.map((i) => (getTvGenres()[i] ? getTvGenres()[i].name : null)).join(', ')}
                          </span>
                        </div>
                      </Item>
                    </Grow>
                  );
                })}
              </ItemWrapper>
            </>
          )}

          {/* {credits.cast?.filter((a) => a.media_type === 'movie').length > 18 && (
          <div onClick={() => handleMore('movies')}>{moviesCount > 20 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}</div>
        )} */}
        </BackgroundCont>
      </>
    );
  }
}
export default Browse;
