import React, { useState, useEffect } from 'react';
import {
  ItemWrapper,
  Item,
  Item2,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  Sec,
  SeeMore,
  Posters,
  PosterWrapper,
  CenterTitle,
  PhotoGallery,
  OutWrp,
  CloseButton,
  OpenFull,
  VideoWrapper,
  ResponsiveVideoOuter,
  ResponsiveVideoWrapper,
  MovieInfo2,
  ImagesTrigger,
  SocialHr,
  BackgroundCont,
  GoBack,
  Center,
  KeywordTitle,
  SortWrapper,
  FilterWrapper,
  FilterInner,
  FilterTitle,
  Chips,
} from '../styles/searchstyles';
import Grow from '@material-ui/core/Grow';
import EmptyPosterBig from '../../img/empty_poster_b.jpg';
import EmptyActorBig from '../../img/empty_actor_b.jpg';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Pagination from '@material-ui/lab/Pagination';
import PaginationItem from '@material-ui/lab/PaginationItem';
import { useLocation, Link } from 'react-router-dom';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import TextField from '@material-ui/core/TextField';

function Find(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [isLoaded3, setIsLoaded3] = useState(false);
  const [isLoaded4, setIsLoaded4] = useState(false);
  const [isLoaded5, setIsLoaded5] = useState(false);
  const [item, setItems] = useState(false);
  const [keyword, setKeyword] = useState(false);
  const [byPopState, setByPopState] = useState(true);
  const [byDateState, setByDateState] = useState(true);
  const [byTitleState, setByTitleState] = useState(true);
  const [byPop, setByPop] = useState(true);
  const [byDate, setByDate] = useState(false);
  const [byTitle, setByTitle] = useState(false);
  const [sort, setSort] = useState('popularity.desc');
  const [page, setPage] = useState(1);
  const [filter, setFilter] = useState(false);
  const [visible, setVisible] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  //filtry
  const [type, setType] = useState('movie');
  const [genres, setGenres] = useState([]);
  const [yearFrom, setYearFrom] = useState('');
  const [yearTo, setYearTo] = useState(2020);
  const [genresType, setGenresType] = useState('|');
  const [chipData, setChipData] = useState([
    { id: 28, name: 'Akcja' },
    { id: 12, name: 'Przygodowy' },
    { id: 16, name: 'Animacja' },
    { id: 35, name: 'Komedia' },
    { id: 80, name: 'Kryminał' },
    { id: 99, name: 'Dokumentalny' },
    { id: 18, name: 'Dramat' },
    { id: 10751, name: 'Familijny' },
    { id: 14, name: 'Fantasy' },
    { id: 36, name: 'Historyczny' },
    { id: 27, name: 'Horror' },
    { id: 10402, name: 'Muzyczny' },
    { id: 9648, name: 'Tajemnica' },
    { id: 10749, name: 'Romans' },
    { id: 878, name: 'Sci-Fi' },
    { id: 10770, name: 'film TV' },
    { id: 53, name: 'Thriller' },
    { id: 10752, name: 'Wojenny' },
    { id: 37, name: 'Western' },
  ]);
  const [chipData2, setChipData2] = useState([
    { id: 10759, name: 'Akcja i Przygoda' },
    { id: 16, name: 'Animacja' },
    { id: 35, name: 'Komedia' },
    { id: 80, name: 'Kryminał' },
    { id: 99, name: 'Dokumentalny' },
    { id: 18, name: 'Dramat' },
    { id: 10751, name: 'Familijny' },
    { id: 10762, name: 'Dla dzieci' },
    { id: 9648, name: 'Tajemnica' },
    { id: 10763, name: 'Wiadomości' },
    { id: 10764, name: 'Reality' },
    { id: 10765, name: 'Sci-Fi & Fantasy' },
    { id: 10766, name: 'Opera mydlana' },
    { id: 10767, name: 'Rozmowy' },
    { id: 10768, name: 'Wojna i Polityka' },
    { id: 37, name: 'Western' },
  ]);
  const api_key = '58666352553834b5820ef436680fb2e5';
  const getMovieGenres = () => ({
    28: { name: 'Akcja' },
    12: { name: 'Przygodowy' },
    16: { name: 'Animacja' },
    35: { name: 'Komedia' },
    80: { name: 'Kryminał' },
    99: { name: 'Dokumentalny' },
    18: { name: 'Dramat' },
    10751: { name: 'Familijny' },
    14: { name: 'Fantasy' },
    36: { name: 'Historyczny' },
    27: { name: 'Horror' },
    10402: { name: 'Muzyczny' },
    9648: { name: 'Tajemnica' },
    10749: { name: 'Romans' },
    878: { name: 'Sci-Fi' },
    10770: { name: 'film TV' },
    53: { name: 'Thriller' },
    10752: { name: 'Wojenny' },
    37: { name: 'Western' },
  });
  const getTvGenres = () => ({
    10759: { name: 'Akcja i Przygoda' },
    16: { name: 'Animacja' },
    35: { name: 'Komedia' },
    80: { name: 'Kryminał' },
    99: { name: 'Dokumentalny' },
    18: { name: 'Dramat' },
    10751: { name: 'Familijny' },
    10762: { name: 'Dla dzieci' },
    9648: { name: 'Tajemnica' },
    10763: { name: 'Wiadomości' },
    10764: { name: 'Reality' },
    10765: { name: 'Sci-Fi & Fantasy' },
    10766: { name: 'Opera mydlana' },
    10767: { name: 'Rozmowy' },
    10768: { name: 'Wojna i Polityka' },
    37: { name: 'Western' },
  });
  const toggleGenre = (id) => {
    if (genres.includes(id)) {
      setGenres((items) => items.filter((item) => item !== id));
    } else {
      setGenres((items) => [...items, id]);
    }
  };

  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPosterBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActorBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    }
  };
  let location = useLocation();
  useEffect(() => {
    if (visible) {
      fetch(
        `https://api.themoviedb.org/3/discover/${type}?api_key=${api_key}&page=${page}&sort_by=${sort}&with_genres=${genres.join(genresType)}&${
          type === 'movie' ? 'primary_release_date.gte' : 'first_air_date.gte'
        }=${yearFrom > 999 ? yearFrom : 1000}-01-01&${type === 'movie' ? 'primary_release_date.lte' : 'first_air_date.lte'}=${
          yearTo > 999 ? yearTo : 1000
        }-12-31&language=pl-PL`
      )
        .then((res) => res.json())
        .then(
          (result) => {
            setItems(result);
            setIsLoaded(true);
          },
          (error) => {
            setIsLoaded(false);
            setError(error);
          }
        );
    }
  }, [location, filter, page, sort]);
  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (false) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        <GoBack>
          <ArrowBackIcon
            fontSize='large'
            onClick={() => {
              if (props.history.length > 2) {
                props.history.goBack();
              } else props.history.replace('');
            }}
          />
        </GoBack>
        <BackgroundCont style={{ minHeight: 'calc(100vh - 149px)' }}>
          <Sec>
            Wyszukaj{' '}
            <FormControl>
              <Select
                labelId='searchSelect'
                id='searchSelect'
                value={type}
                onChange={(e) => {
                  setType(e.target.value);
                  setItems(false);
                  setGenres([]);
                }}
                style={{ minWidth: '150px', fontSize: '35px', marginLeft: '20px' }}>
                <MenuItem value='movie'>Film</MenuItem>
                <MenuItem value='tv'>Serial</MenuItem>
              </Select>
            </FormControl>
          </Sec>
          <Grid container spacing={0} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
            <Grid item xs={12} sm={6} md={6}>
              <FilterWrapper>
                <div>
                  <FilterTitle>Gatunki:</FilterTitle>
                  <FilterInner>
                    {type === 'movie' && (
                      <>
                        {chipData.map((data) => (
                          <Chips
                            style={{ margin: '2px' }}
                            selected={genres.includes(data.id)}
                            label={data.name}
                            variant={genres.includes(data.id) ? 'default' : 'outlined'}
                            key={data.id}
                            onClick={() => toggleGenre(data.id)}
                          />
                        ))}
                      </>
                    )}
                    {type === 'tv' && (
                      <>
                        {chipData2.map((data) => (
                          <Chips
                            style={{ margin: '2px' }}
                            selected={genres.includes(data.id)}
                            label={data.name}
                            variant={genres.includes(data.id) ? 'default' : 'outlined'}
                            key={data.id}
                            onClick={() => toggleGenre(data.id)}
                          />
                        ))}
                      </>
                    )}
                  </FilterInner>
                  <FilterTitle style={{ fontSize: '17px', marginTop: '30px' }}>Typ Wyszukiwania Gatunków</FilterTitle>
                  <FormControl>
                    <Select
                      labelId='genresTypeSelect'
                      id='genresTypeSelect'
                      value={genresType}
                      onChange={(e) => {
                        setItems(false);
                        setGenresType(e.target.value);
                      }}
                      style={{ minWidth: '150px', fontSize: '13px', margin: '5px' }}>
                      <MenuItem value=','>Film/Serial musi posiadać wszystkie gatunki</MenuItem>
                      <MenuItem value='|'>Film/Serial nie musi posiadać wszystkich gatunków</MenuItem>
                    </Select>
                  </FormControl>
                </div>
              </FilterWrapper>
            </Grid>
            <Grid item xs={12} sm={6} md={6}>
              <FilterWrapper>
                <div>
                  <FilterTitle>Rok:</FilterTitle>
                  <FilterInner>
                    <TextField
                      id='standard-number'
                      label='od'
                      value={yearFrom}
                      onChange={(e) => setYearFrom(e.target.value)}
                      type='number'
                      InputLabelProps={{
                        shrink: true,
                      }}
                      style={{ margin: '5px' }}
                    />
                    <TextField
                      id='standard-number'
                      label='do'
                      value={yearTo}
                      onChange={(e) => setYearTo(e.target.value)}
                      type='number'
                      InputLabelProps={{
                        shrink: true,
                      }}
                      style={{ margin: '5px' }}
                    />
                  </FilterInner>
                </div>
              </FilterWrapper>
            </Grid>
          </Grid>

          {/* <div
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'flex-start',
              flexDirection: 'column',
              width: '50%',
              padding: '30px',
            }}>
            <div>Uwzględnić filmy dla dorosłych?</div>
            <FormControl>
              <Select labelId='demo-simple-select-label' id='demo-simple-select' value={adult} onChange={(e) => setAdult(e.target.value)}>
                <MenuItem value={1}>Tak</MenuItem>
                <MenuItem value={0}>Nie</MenuItem>
              </Select>
            </FormControl>
         
          </div> */}
          <Button
            variant='outlined'
            onClick={() => {
              setFilter(!filter);
              setVisible(true);
            }}
            style={{ marginLeft: '30px' }}>
            Pokaż
          </Button>
          <div style={{ borderBottom: '1px solid #ddd', width: '100%', marginTop: '15px', marginBottom: '15px' }}></div>
          <SortWrapper>
            <div>Sortuj Według</div>
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
              }}>
              <span
                onClick={() => {
                  setByPopState(!byPopState);
                  setByPop(true);
                  setByDate(false);
                  setByTitle(false);
                  setSort(`${byPopState ? 'popularity.asc' : 'popularity.desc'}`);
                }}>
                {byPop ? <span style={{ color: '#153856', fontWeight: 'bold' }}>Popularności</span> : 'Popularności'}
                {byPopState ? <ArrowDropDownIcon /> : <ArrowDropUpIcon />}
              </span>
              <span
                onClick={() => {
                  setByDateState(!byDateState);
                  setByPop(false);
                  setByDate(true);
                  setByTitle(false);
                  setSort(`${byDateState ? 'primary_release_date.asc' : 'primary_release_date.desc'}`);
                }}>
                {byDate ? <span style={{ color: '#153856', fontWeight: 'bold' }}>Daty</span> : 'Daty'}
                {byDateState ? <ArrowDropDownIcon /> : <ArrowDropUpIcon />}
              </span>
              <span
                onClick={() => {
                  setByTitleState(!byTitleState);
                  setByPop(false);
                  setByDate(false);
                  setByTitle(true);
                  setSort(`${byTitleState ? 'original_title.asc' : 'original_title.desc'}`);
                }}>
                {byTitle ? <span style={{ color: '#153856', fontWeight: 'bold' }}>Tytułu</span> : 'Tytułu'}
                {byTitleState ? <ArrowDropDownIcon /> : <ArrowDropUpIcon />}
              </span>
            </div>
          </SortWrapper>
          {item && (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <Pagination page={item.page} count={item.total_pages} onChange={(e, value) => setPage(value)} />
            </div>
          )}
          {type === 'movie' ? (
            <ItemWrapper>
              {item?.results?.map((i, index) => {
                i.media_type = 'movie';
                return (
                  <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                    <Item to={encodeURI(`/${i.media_type}/${i.id}-${i.title.toLowerCase().split(' ').join('-')}`)}>
                      {havePhoto(i)}
                      <div className='tit2'>
                        {i.title} {i.release_date && `(${i.release_date.slice(0, 4)})`}
                        <br />
                        <span className='character'>
                          {i.genre_ids?.length > 0 &&
                            i.genre_ids.map((inn) => (getMovieGenres()[inn] ? getMovieGenres()[inn].name : null)).join(', ')}
                        </span>
                      </div>
                    </Item>
                  </Grow>
                );
              })}
            </ItemWrapper>
          ) : (
            <ItemWrapper>
              {item?.results?.map((i, index) => {
                i.media_type = 'tv';
                return (
                  <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                    <Item to={encodeURI(`/${i.media_type}/${i.id}-${i.name.toLowerCase().split(' ').join('-')}`)}>
                      {havePhoto(i)}
                      <div className='tit2'>
                        {i.name} {i.first_air_date && `(${i.first_air_date.slice(0, 4)})`}
                        <br />
                        <span className='character'>
                          {i.genre_ids?.length > 0 && i.genre_ids.map((inn) => (getTvGenres()[inn] ? getTvGenres()[inn].name : null)).join(', ')}
                        </span>
                      </div>
                    </Item>
                  </Grow>
                );
              })}
            </ItemWrapper>
          )}

          {item && (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <Pagination page={item.page} count={item.total_pages} onChange={(e, value) => setPage(value)} />
            </div>
          )}
        </BackgroundCont>
      </>
    );
  }
}
export default Find;
