import React, { useState, useEffect } from 'react';
import {
  ItemWrapper,
  Item,
  Item2,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  Sec,
  SeeMore,
  Posters,
  PosterWrapper,
  CenterTitle,
  PhotoGallery,
  OutWrp,
  CloseButton,
  OpenFull,
  VideoWrapper,
  ResponsiveVideoOuter,
  ResponsiveVideoWrapper,
  MovieInfo2,
  ImagesTrigger,
  SocialHr,
  BackgroundCont,
  GoBack,
  Center,
  SortWrapper,
  KeywordTitle,
} from '../styles/searchstyles';
import Grow from '@material-ui/core/Grow';
import EmptyPosterBig from '../../img/empty_poster_b.jpg';
import EmptyActorBig from '../../img/empty_actor_b.jpg';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { Link } from 'react-router-dom';
import Pagination from '@material-ui/lab/Pagination';
import PaginationItem from '@material-ui/lab/PaginationItem';
import { useLocation } from 'react-router-dom';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';

function Keywords(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [item, setItems] = useState(false);
  const [keyword, setKeyword] = useState(false);
  const [byPopState, setByPopState] = useState(true);
  const [byDateState, setByDateState] = useState(true);
  const [byTitleState, setByTitleState] = useState(true);
  const [byPop, setByPop] = useState(true);
  const [byDate, setByDate] = useState(false);
  const [byTitle, setByTitle] = useState(false);
  const [sort, setSort] = useState(false);

  const api_key = '58666352553834b5820ef436680fb2e5';
  const getMovieGenres = () => ({
    28: { name: 'Akcja' },
    12: { name: 'Przygodowy' },
    16: { name: 'Animacja' },
    35: { name: 'Komedia' },
    80: { name: 'Kryminał' },
    99: { name: 'Dokumentalny' },
    18: { name: 'Dramat' },
    10751: { name: 'Familijny' },
    14: { name: 'Fantasy' },
    36: { name: 'Historyczny' },
    27: { name: 'Horror' },
    10402: { name: 'Muzyczny' },
    9648: { name: 'Tajemnica' },
    10749: { name: 'Romans' },
    878: { name: 'Sci-Fi' },
    10770: { name: 'film TV' },
    53: { name: 'Thriller' },
    10752: { name: 'Wojenny' },
    37: { name: 'Western' },
  });

  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPosterBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActorBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    }
  };
  const getKeywordName = (id) => {
    fetch(`https://api.themoviedb.org/3/keyword/${id}?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setKeyword(result);
          setIsLoaded2(true);
          document.title = result.name.toUpperCase();
        },
        (error) => {
          setIsLoaded2(false);
          setError(error);
        }
      );
  };
  let location = useLocation();
  useEffect(() => {
    if (!window.location.search || window.location.search === '') window.location.search = '?page=1&sort=popularity.desc';
    else {
      const url = location.pathname;
      const search = location.search;
      const regex = /(\d+)/;
      const regexPage = /page=(\d+)/;
      const regexSort = /sort=(.+\..+)/;
      const keyword_id = url.match(regex) && url.match(regex)[0];
      let page = search.match(regexPage) && search.match(regexPage)[1];
      let sortT = search.match(regexSort) && search.match(regexSort)[1];
      if ((page > 500 && !sortT) || (!page && !sortT)) window.location.search = `?page=1&sort=popularity.desc`;
      else if (!sortT) window.location.search = `?page=${page}&sort=popularity.desc`;
      else if (page > 500 || !page) window.location.search = `?page=1&sort=${sortT}`;
      setSort(sortT);
      getKeywordName(keyword_id);

      fetch(`https://api.themoviedb.org/3/discover/movie?api_key=${api_key}&page=${page}&with_keywords=${keyword_id}&sort_by=${sortT}&language=pl-PL`)
        .then((res) => res.json())
        .then(
          (result) => {
            setItems(result);
            setIsLoaded(true);
          },
          (error) => {
            setIsLoaded(false);
            setError(error);
          }
        );
      if (sortT === 'popularity.asc' || sortT === 'popularity.desc') {
        setByPop(true);
        setByDate(false);
        setByTitle(false);
        if (sortT === 'popularity.asc') setByPopState(false);
        else if (sortT === 'popularity.desc') setByPopState(true);
      } else if (sortT === 'primary_release_date.asc' || sortT === 'primary_release_date.desc') {
        setByPop(false);
        setByDate(true);
        setByTitle(false);
        if (sortT === 'primary_release_date.asc') setByDateState(false);
        else if (sortT === 'primary_release_date.desc') setByDateState(true);
      } else if (sortT === 'original_title.asc' || sortT === 'original_title.desc') {
        setByPop(false);
        setByDate(false);
        setByTitle(true);
        if (sortT === 'original_title.asc') setByTitleState(false);
        else if (sortT === 'original_title.desc') setByTitleState(true);
      } else {
        window.location.search = `?page=${page}&sort=popularity.desc`;
      }
      window.scrollTo(0, 0);
    }
  }, [location]);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        <GoBack>
          <ArrowBackIcon
            fontSize='large'
            onClick={() => {
              if (props.history.length > 2) {
                props.history.goBack();
              } else props.history.replace('');
            }}
          />
        </GoBack>
        <BackgroundCont style={{ minHeight: 'calc(100vh - 149px)' }}>
          {item.results.length > 0 && (
            <Sec>
              <KeywordTitle>
                Filmy{' '}
                {isLoaded2 ? (
                  <>
                    oznaczone tagiem: <span style={{ fontWeight: 'bold', marginLeft: '10px' }}>{keyword.name}</span>
                  </>
                ) : null}
              </KeywordTitle>
            </Sec>
          )}
          <SortWrapper>
            <div>Sortuj Według</div>
            <div
              style={{
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
              }}>
              <span
                onClick={() => {
                  setByPopState(!byPopState);
                  setByPop(true);
                  setByDate(false);
                  setByTitle(false);
                  props.history.push(`?page=${item.page}&sort=${byPopState ? 'popularity.asc' : 'popularity.desc'}`);
                }}>
                {byPop ? <span style={{ color: '#153856', fontWeight: 'bold' }}>Popularności</span> : 'Popularności'}
                {byPopState ? <ArrowDropDownIcon /> : <ArrowDropUpIcon />}
              </span>
              <span
                onClick={() => {
                  setByDateState(!byDateState);
                  setByPop(false);
                  setByDate(true);
                  setByTitle(false);
                  props.history.push(`?page=${item.page}&sort=${byDateState ? 'primary_release_date.asc' : 'primary_release_date.desc'}`);
                }}>
                {byDate ? <span style={{ color: '#153856', fontWeight: 'bold' }}>Daty</span> : 'Daty'}
                {byDateState ? <ArrowDropDownIcon /> : <ArrowDropUpIcon />}
              </span>
              <span
                onClick={() => {
                  setByTitleState(!byTitleState);
                  setByPop(false);
                  setByDate(false);
                  setByTitle(true);
                  props.history.push(`?page=${item.page}&sort=${byTitleState ? 'original_title.asc' : 'original_title.desc'}`);
                }}>
                {byTitle ? <span style={{ color: '#153856', fontWeight: 'bold' }}>Tytułu</span> : 'Tytułu'}
                {byTitleState ? <ArrowDropDownIcon /> : <ArrowDropUpIcon />}
              </span>
            </div>
          </SortWrapper>

          {item && (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <Pagination
                page={item.page}
                count={item.total_pages}
                renderItem={(item) => <PaginationItem component={Link} to={`?page=${item.page}&sort=${sort}`} {...item} />}
              />
            </div>
          )}
          <ItemWrapper>
            {item.results.map((item, index) => {
              item.media_type = 'movie';
              return (
                <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                  <Item to={encodeURI(`/${item.media_type}/${item.id}-${item.title.toLowerCase().split(' ').join('-')}`)}>
                    {havePhoto(item)}
                    <div className='tit2'>
                      {item.title} {item.release_date && `(${item.release_date.slice(0, 4)})`}
                      <br />
                      <span className='character'>
                        {item.genre_ids?.length > 0 && item.genre_ids.map((i) => (getMovieGenres()[i] ? getMovieGenres()[i].name : null)).join(', ')}
                      </span>
                    </div>
                  </Item>
                </Grow>
              );
            })}
          </ItemWrapper>
          {item && (
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <Pagination
                page={item.page}
                count={item.total_pages}
                renderItem={(item) => <PaginationItem component={Link} to={`?page=${item.page}&sort=${sort}`} {...item} />}
              />
            </div>
          )}
        </BackgroundCont>
      </>
    );
  }
}
export default Keywords;
