import React, { useState, useEffect } from 'react';
import {
  ItemWrapper,
  Item,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  GoBack,
  Sec,
  SeeMore,
  Posters,
  PosterWrapper,
  CenterTitle,
  PhotoGallery,
  OutWrp,
  CloseButton,
  OpenFull,
  VideoWrapper,
  ResponsiveVideoOuter,
  ResponsiveVideoWrapper,
  MovieInfo2,
  ImagesTrigger,
  SocialHr,
  BackgroundCont,
  Item2,
} from '../styles/searchstyles';
import { Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import EmptyPosterBig from '../../img/empty_poster_b.jpg';
import EmptyActorBig from '../../img/empty_actor_b.jpg';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import CloseIcon from '@material-ui/icons/Close';
import Grow from '@material-ui/core/Grow';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import ImdbIcon from '../../img/imdb_icon.png';
import TvdbIcon from '../../img/tvdb_icon.png';
import HomeIcon from '@material-ui/icons/Home';
import { ReactComponent as TmdbIcon } from '../../img/tmdb_icon.svg';
import FastAverageColor from 'fast-average-color';
import { useLocation } from 'react-router-dom';

function Detailsmovie(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [isLoaded3, setIsLoaded3] = useState(false);
  const [isLoaded4, setIsLoaded4] = useState(false);
  const [item, setItem] = useState('');
  const [credits, setCredits] = useState('');
  const [actorsCount, setActorsCount] = useState(18);
  const [showImages, setShowImages] = useState(false);
  const [videos, setVideos] = useState('');
  const [art1, setArt1] = useState(6);
  const [art2, setArt2] = useState(6);
  const [vid, setVid] = useState(2);
  const [galery, setGalery] = useState(false);
  const [modalImg, setModalImg] = useState(false);
  const [fanarts, setFanarts] = useState('');
  const [averageColor, setAverageColor] = useState('');
  const [tv_idd, setTvId] = useState('');
  const api_key = '58666352553834b5820ef436680fb2e5';
  const url = props.match.params.id;
  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPosterBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActorBig} style={{ width: '100%', maxWidth: '450px' }} />;
      }
    }
  };
  const handleMoreVideos = () => {
    if (vid === 1000) setVid(2);
    else setVid(1000);
  };
  const handleMoreImg = (a) => {
    switch (a) {
      case 1:
        if (art1 === 1000) setArt1(6);
        else setArt1(1000);
        break;
      case 2:
        if (art2 === 1000) setArt2(6);
        else setArt2(1000);
        break;
      default:
        break;
    }
  };
  const handleMore = () => {
    if (actorsCount === 1000) setActorsCount(18);
    else setActorsCount(1000);
  };
  const handleOpen = (src) => {
    setModalImg(src);
    setGalery(true);
  };
  const handleClose = () => {
    setGalery(false);
  };
  const handleShowImages = () => {
    setShowImages(true);
  };
  const getGenres = () => ({
    10759: { name: 'Akcja i Przygoda' },
    16: { name: 'Animacja' },
    35: { name: 'Komedia' },
    80: { name: 'Kryminał' },
    99: { name: 'Dokumentalny' },
    18: { name: 'Dramat' },
    10751: { name: 'Familijny' },
    10762: { name: 'Dla dzieci' },
    9648: { name: 'Mystery' },
    10763: { name: 'Wiadomości' },
    10764: { name: 'Reality' },
    10765: { name: 'Sci-Fi & Fantasy' },
    10766: { name: 'Opera mydlana' },
    10767: { name: 'Rozmowy' },
    10768: { name: 'Wojna i Polityka' },
    37: { name: 'Western' },
  });
  const getStatus = (status) => {
    if (status === 'Canceled') return 'Anulowany';
    else if (status === 'Ended') return 'Zakończony';
    else if (status === 'In Production') return 'W produkcji';
    else if (status === 'Pilot') return 'Pilot';
    else if (status === 'Planned') return 'Planowany';
    else if (status === 'Post Production') return 'Postprodukcja';
    else if (status === 'Released') return 'Wydany';
    else if (status === 'Rumored') return 'Pogłoski';
    else if (status === 'Returning Series') return 'Powracający sezon';
    else if (status === 'Released') return 'Powracający sezon';
    else return status;
  };
  const getAverageColor = (href) => {
    const fac = new FastAverageColor();
    let imageURL = href;

    let downloadedImg = new Image();
    downloadedImg.crossOrigin = 'Anonymous';
    downloadedImg.src = imageURL;
    fac
      .getColorAsync(downloadedImg)
      .then((color) => {
        setAverageColor(color.rgb);
      })
      .catch((e) => {
        console.log(e);
      });
  };
  const queries = () => {
    const url_to_match = url;
    const regex = /(\d+)/;
    const tv_id = url_to_match.match(regex) && url_to_match.match(regex)[0];
    setTvId(tv_id);

    fetch(
      `https://api.themoviedb.org/3/tv/${tv_id}?api_key=${api_key}&append_to_response=external_ids,images,recommendations&include_image_language=en,pl,null&language=pl-PL`
    )
      .then((res) => res.json())
      .then(
        (result) => {
          result.media_type = 'tv';
          setItem(result);
          setIsLoaded(true);
          if (tv_id && result.name) {
            if (encodeURI(props.match.url) !== encodeURI(`/tv/${tv_id}-${result.name.toLowerCase().split(' ').join('-')}`)) {
              props.history.replace(encodeURI(`/tv/${tv_id}-${result.name.toLowerCase().split(' ').join('-')}`));
            }
          }
          if (result.name && result.first_air_date) {
            document.title = `Serial: ${result.name} (${result.first_air_date.slice(0, 4)})`;
          } else {
            document.title = 'InfCinema';
          }
          getAverageColor(`https://image.tmdb.org/t/p/w92${result.poster_path}`);
          setFanarts(result.images);
        },
        (error) => {
          setIsLoaded(false);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/tv/${tv_id}/credits?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setCredits(result);
          setIsLoaded2(true);
        },
        (error) => {
          setIsLoaded2(false);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/tv/${tv_id}/videos?api_key=${api_key}`)
      .then((res) => res.json())
      .then(
        (result) => {
          setVideos(result);
          setIsLoaded4(true);
        },
        (error) => {
          setIsLoaded4(false);
          setError(error);
        }
      );
    window.scrollTo(0, 0);
  };
  let location = useLocation();
  useEffect(() => {
    setIsLoaded(false);
    setIsLoaded2(false);
    setIsLoaded3(false);
    setIsLoaded4(false);
    queries();
  }, [location]);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded || !isLoaded2) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        {' '}
        <GoBack>
          <ArrowBackIcon fontSize='large' onClick={() => (props.history.length > 2 ? props.history.goBack() : props.history.replace(''))} />
        </GoBack>
        <PhotoGallery maxWidth='xs' fullWidth={true} open={galery} onClose={handleClose}>
          <img alt='Modal' src={modalImg} style={{ maxWidth: '100%', maxHeight: '100%' }} />
          <CloseButton onClick={handleClose}>
            <CloseIcon fontSize='large' />
          </CloseButton>
          <OpenFull target='_blank' href={modalImg}>
            Otwórz w pełnej rozdzielczości
          </OpenFull>
        </PhotoGallery>
        <BackgroundCont
          container
          spacing={0}
          url={`https://image.tmdb.org/t/p/w1280${item.backdrop_path && item.backdrop_path}`}
          colorr={averageColor.slice(4).slice(0, averageColor.slice(4).length - 1)}>
          <PhotoContainer item xs={12} md={6} lg={4}>
            {havePhoto(item, 'big')}
            <TitleItem>{item.name}</TitleItem>
            <span
              style={{
                width: '100%',
                maxWidth: '450px',
                backgroundColor: 'white',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                paddingBottom: '5px',
              }}>
              {item.first_air_date && ` ( ${item.first_air_date.slice(0, 4)} )`}
            </span>
            {item.external_ids && (
              <SocialHr>
                {item.homepage && (
                  <a href={item.homepage} target='_blank' rel='noopener noreferrer'>
                    <HomeIcon style={{ margin: '5px', color: '#333' }} />
                  </a>
                )}
                {item.external_ids.instagram_id && (
                  <a href={`https://www.instagram.com/${item.external_ids.instagram_id}`} target='_blank' rel='noopener noreferrer'>
                    <InstagramIcon style={{ margin: '5px', color: '#E1306C' }} />
                  </a>
                )}
                {item.external_ids.facebook_id && (
                  <a href={`https://www.facebook.com/${item.external_ids.facebook_id}`} target='_blank' rel='noopener noreferrer'>
                    <FacebookIcon style={{ margin: '5px', color: '#4267B2' }} />
                  </a>
                )}
                {item.external_ids.twitter_id && (
                  <a href={`https://www.twitter.com/${item.external_ids.twitter_id}`} target='_blank' rel='noopener noreferrer'>
                    <TwitterIcon style={{ margin: '5px', color: '#1da1f2' }} />
                  </a>
                )}
                {item.id && (
                  <a href={`https://www.themoviedb.org/tv/${item.id}`} target='_blank' rel='noopener noreferrer'>
                    <TmdbIcon alt='imdb' src={ImdbIcon} height='20px' style={{ margin: '5px' }} />
                  </a>
                )}
                {item.external_ids.imdb_id && (
                  <a href={`https://www.imdb.com/title/${item.external_ids.imdb_id}`} target='_blank' rel='noopener noreferrer'>
                    <img alt='imdb' src={ImdbIcon} height='20px' style={{ margin: '5px' }} />
                  </a>
                )}
                {item.external_ids.tvdb_id && (
                  <a href={`https://www.thetvdb.com/dereferrer/series/${item.external_ids.tvdb_id}`} target='_blank' rel='noopener noreferrer'>
                    <img alt='imdb' src={TvdbIcon} height='20px' style={{ margin: '5px' }} />
                  </a>
                )}
              </SocialHr>
            )}
          </PhotoContainer>
          <InfoContainer item xs={12} md={6} lg={8} xl={8}>
            <MovieInfo style={{ marginTop: '30px' }}>
              Opis: <br />
              <span>{item.overview ? item.overview : 'Brak polskiego opisu'}</span>
            </MovieInfo>
            {item.tagline && (
              <MovieInfo>
                Slogan: <span>{item.tagline}</span>
              </MovieInfo>
            )}
            <div style={{ borderTop: '1px solid gray', width: '100%', marginBottom: '5px' }} />
            <div style={{ display: 'flex', flexWrap: 'wrap' }}>
              <MovieInfo>
                Typ: <span>{item.name ? 'Serial' : 'Brak danych'}</span>
              </MovieInfo>{' '}
              <MovieInfo>
                Premiera: <span>{item.first_air_date ? item.first_air_date : 'Brak danych'}</span>
              </MovieInfo>{' '}
              <MovieInfo>
                Czas trwania odcinka:{' '}
                {item.episode_run_time ? (
                  <span>{item.episode_run_time[0] ? `${item.episode_run_time[0]} minut` : 'Brak danych'}</span>
                ) : (
                  <span>Brak danych</span>
                )}
              </MovieInfo>
              <MovieInfo>
                Status: <span>{item.status ? getStatus(item.status) : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                Język: <span>{item.languages?.length > 0 ? item.languages.map((i) => i).join(', ') : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                {item.origin_country?.length > 1 ? 'Kraje: ' : 'Kraj: '}
                <span>{item.origin_country?.length > 0 ? item.origin_country.map((i) => i).join(', ') : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                Twórcy:{' '}
                <span>
                  {item.created_by?.length > 0
                    ? item.created_by.map((i, index) => (
                        <span key={index}>
                          <Link to={encodeURI(`/person/${i.id}`)}>{i.name}</Link>
                          {index === item.created_by.length - 1 ? '' : ' , '}
                        </span>
                      ))
                    : 'Brak danych'}
                </span>
              </MovieInfo>
              <MovieInfo>
                {item.production_companies?.length > 1 ? 'Wytwórnie: ' : 'Wytwórnia: '}
                <span>{item.production_companies?.length > 0 ? item.production_companies.map((i) => i.name).join(', ') : 'Brak danych'}</span>
              </MovieInfo>{' '}
              <MovieInfo>
                Sezonów: <span>{item.number_of_seasons ? item.number_of_seasons : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                Odcinków: <span>{item.number_of_episodes ? item.number_of_episodes : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo>
                Gatunek: <span>{item.genres?.length > 0 ? item.genres.map((i) => i.name).join(', ') : 'Brak danych'}</span>
              </MovieInfo>
              <MovieInfo style={{ display: 'flex', alignItems: 'flex-start' }}>
                Stacje:
                {item.networks?.length > 0 ? (
                  <span
                    style={{
                      display: 'flex',
                      flexWrap: 'wrap',
                      justifyContent: 'flex-start',
                      alignItems: 'center',
                      // marginTop: '5px',
                      marginLeft: '15px',
                    }}>
                    {item.networks?.map((i, index) => (
                      <img
                        key={index}
                        alt={i.name}
                        style={{ margin: '2px 8px' }}
                        height='30px'
                        src={`https://image.tmdb.org/t/p/w185/${i.logo_path}`}
                      />
                    ))}
                  </span>
                ) : (
                  <span style={{ alignSelf: 'flex-end' }}> Brak danych</span>
                )}
              </MovieInfo>
            </div>
            <div style={{ borderTop: '1px solid gray', width: '100%', marginBottom: '5px', marginTop: '5px' }} />
            {isLoaded4 && (
              <MovieInfo2 style={{ width: '100%' }}>
                {videos.results?.length > 0 && (
                  <div
                    style={{
                      display: 'flex',
                      flexDirection: 'column',
                      alignItems: 'flex-start',
                      width: '100%',
                    }}>
                    {videos.results?.filter((item) => item.type === 'Trailer' && item.site === 'YouTube').length > 0 && (
                      <div style={{ margin: '20px 15px 10px 15px', fontSize: '30px' }}>Zwiatun:</div>
                    )}
                    {videos.results && (
                      <VideoWrapper>
                        {videos.results
                          .filter((item) => item.type === 'Trailer' && item.site === 'YouTube')
                          .slice(0, vid)
                          .map((video, index) => (
                            <ResponsiveVideoOuter min={(vid > 2).toString()} key={index}>
                              <ResponsiveVideoWrapper>
                                <iframe
                                  style={{ position: 'absolute', top: '0', left: '0', right: '0', bottom: '0', width: '100%', height: '100%' }}
                                  allowFullScreen='allowfullscreen'
                                  mozallowfullscreen='mozallowfullscreen'
                                  msallowfullscreen='msallowfullscreen'
                                  oallowfullscreen='oallowfullscreen'
                                  webkitallowfullscreen='webkitallowfullscreen'
                                  title={video.name}
                                  src={`https://www.youtube.com/embed/${video.key}`}></iframe>
                              </ResponsiveVideoWrapper>
                            </ResponsiveVideoOuter>
                          ))}
                      </VideoWrapper>
                    )}
                  </div>
                )}
                {videos.results?.filter((item) => item.type === 'Trailer' && item.site === 'YouTube').length > 2 && (
                  <Grid item xs={12} onClick={() => handleMoreVideos()}>
                    {vid > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
                  </Grid>
                )}
              </MovieInfo2>
            )}
          </InfoContainer>
        </BackgroundCont>
        {item.seasons && <Sec>Sezony</Sec>}
        <ItemWrapper>
          {item.seasons?.map((i, index) => {
            i.media_type = 'tv';
            return (
              <Item key={index} to={encodeURI(`/tv/${url}/season/${i.season_number}`)}>
                {havePhoto(i)}
                <div className='tit2'>{i.name}</div>
              </Item>
            );
          })}
        </ItemWrapper>
        {credits.cast && <Sec>Obsada</Sec>}
        <ItemWrapper>
          {credits.cast?.slice(0, actorsCount).map((person, index) => {
            return (
              <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                <Item to={encodeURI(`/person/${person.id}-${person.name.toLowerCase().split(' ').join('-')}`)}>
                  {havePhoto(person)}
                  <div className='tit2'>
                    {person.name}
                    <br />
                    <span className='character'>{person.character}</span>
                  </div>
                </Item>
              </Grow>
            );
          })}
        </ItemWrapper>
        {credits.cast?.length > 18 && (
          <div onClick={handleMore}>{actorsCount > 20 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}</div>
        )}
        {item.recommendations?.results?.length > 0 && <Sec>Rekomendacje:</Sec>}
        <ItemWrapper>
          {item.recommendations?.results?.slice(0, 18).map((tv, index) => {
            tv.media_type = 'tv';
            return (
              <Grow in={true} style={{ transformOrigin: '0 0 0' }} {...{ timeout: 50 * index }} key={index}>
                <Item2 to={encodeURI(`/tv/${tv.id}-${tv.name?.toLowerCase().split(' ').join('-')}`)}>
                  {havePhoto(tv)}
                  <div className='tit2'>
                    {tv.name} {tv.first_air_date && `(${tv.first_air_date.slice(0, 4)})`}
                    <br />
                    <span className='character'>
                      {tv.genre_ids?.length > 0 && tv.genre_ids.map((i) => (getGenres()[i] ? getGenres()[i].name : null)).join(', ')}
                    </span>
                  </div>
                </Item2>
              </Grow>
            );
          })}
        </ItemWrapper>
        <Sec style={{ marginBottom: '30px' }}>Grafiki:</Sec>
        {showImages ? (
          <>
            {fanarts && (
              <OutWrp container spacing={0}>
                <CenterTitle item xs={12}>
                  Tła
                </CenterTitle>
                {fanarts.backdrops.slice(0, art1).map((im, index) => {
                  return (
                    <PosterWrapper item xs={6} md={6} lg={2} key={index}>
                      <Posters
                        onClick={() => handleOpen(`https://image.tmdb.org/t/p/original${im.file_path}`)}
                        src={`https://image.tmdb.org/t/p/w300${im.file_path}`}
                        alt={im.id}
                      />
                    </PosterWrapper>
                  );
                })}
                {fanarts.backdrops.length > 6 && (
                  <Grid item xs={12} onClick={() => handleMoreImg(1)}>
                    {art1 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
                  </Grid>
                )}{' '}
              </OutWrp>
            )}
            {fanarts && (
              <OutWrp container spacing={0}>
                <CenterTitle item xs={12}>
                  Plakaty
                </CenterTitle>
                {fanarts.posters.slice(0, art2).map((im, index) => {
                  return (
                    <PosterWrapper item xs={6} md={6} lg={2} key={index}>
                      <Posters
                        onClick={() => handleOpen(`https://image.tmdb.org/t/p/original${im.file_path}`)}
                        src={`https://image.tmdb.org/t/p/w300${im.file_path}`}
                        alt={im.id}
                      />
                    </PosterWrapper>
                  );
                })}
                {fanarts.posters.length > 6 && (
                  <Grid item xs={12} onClick={() => handleMoreImg(2)}>
                    {art2 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
                  </Grid>
                )}
              </OutWrp>
            )}
          </>
        ) : (
          <ImagesTrigger onClick={() => handleShowImages()}>
            Załaduj Grafiki <ExpandMoreIcon fontSize='large' />
          </ImagesTrigger>
        )}
      </>
    );
  }
}

export default Detailsmovie;
